import React from 'react';
import s from "./Footer.module.css";
import cs from '../../common/styles/styles.module.css';
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {
  loadDataFromLocalStorageAC,
  mainTrendType,
  saveDataToLocalStorageAC, setNewNotificationAC
} from "../../store/appReducer";
import ButtonSaveLoad from "../../components/buttonSaveLoad/ButtonSaveLoad";
import ButtonActiveWindow from "../../components/buttonActiveWindow/ButtonActiveWindow";
import TradeHistoryStatistic from "../../components/TradeHistoryStatistic/TradeHistoryStatistic";

const Footer: React.FC = () => {

  const dispatch = useDispatch()

  const trendSmall = useSelector<AppRootStateType, mainTrendType>(
    store => store.app.bitcoinMainInformation.trendSmall)

  const onClickSave = () => {
    dispatch(saveDataToLocalStorageAC())
    dispatch(setNewNotificationAC(
      `Save Data To Local Storage`,
      `Save Data To Local Storage`,
      'WARNING'))
  }

  const onClickLoad = () => {
    dispatch(loadDataFromLocalStorageAC())
    dispatch(setNewNotificationAC(
      `Load Data To Local Storage`,
      `Load Data To Local Storage`,
      'INFO'))
  }

  return (
    <div className={`
    ${cs.commonBody} 
    ${trendSmall === 'BULL' && cs.common__colorBull}
    ${trendSmall === 'BEAR' && cs.common__colorBear}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FLET' && cs.common__colorFlet}
    `}
    >
      <div className={`${cs.commonContainer} ${s.footerContainer}`}>

        <ButtonSaveLoad onClick={onClickLoad} name='Load'/>
        <ButtonSaveLoad onClick={onClickSave} name='Save'/>

        <TradeHistoryStatistic/>

        <ButtonActiveWindow />

        <span>0.2.1</span>
      </div>
    </div>
  )
}

export default Footer