import React from 'react'
import s from "./TradeHistoryStatistic.module.css"
import cs from "../../common/styles/styles.module.css";
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {symbolHoverInformationType} from "../../store/appReducer";

const TradeHistoryStatistic: React.FC = () => {

  const symbolHoverInformation = useSelector<AppRootStateType, symbolHoverInformationType>(
    store => store.app.symbolsInformation.symbolHoverInformation)

  const tradesHistoryPositiveBuy = useSelector<AppRootStateType, number>(
    state => state.app.tradesInformation.tradesHistoryPositiveBuy[symbolHoverInformation])
  const tradesHistoryPositiveSell = useSelector<AppRootStateType, number>(
    state => state.app.tradesInformation.tradesHistoryPositiveSell[symbolHoverInformation])
  const tradesHistoryNegativeBuy = useSelector<AppRootStateType, number>(
    state => state.app.tradesInformation.tradesHistoryNegativeBuy[symbolHoverInformation])
  const tradesHistoryNegativeSell = useSelector<AppRootStateType, number>(
    state => state.app.tradesInformation.tradesHistoryNegativeSell[symbolHoverInformation])


  if (symbolHoverInformation === '') {
    return (<div>{}</div>)
  } else {
    return (
      <span className={cs.commonFlexBody}>
      <span>Positive B/S:</span>
      <span className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}>
        {tradesHistoryPositiveBuy}/{tradesHistoryPositiveSell}
      </span>
      <span>Negative B/S:</span>
      <span className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}>
        {tradesHistoryNegativeBuy}/{tradesHistoryNegativeSell}
      </span>
    </span>
    )
  }
}

export default TradeHistoryStatistic