import React from 'react';
import s from "./Header.module.css";
import cs from '../../common/styles/styles.module.css';
import Timer from "../../components/timer/Timer";
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {mainTrendType} from "../../store/appReducer";
import SearchActivator from "../../components/searchActivator/SearchActivator";
import MainTrendIndicator from "../../components/mainTrendIndicator/MainTrendIndicator";
import ChangeInitVariable from "../../components/changeInitVariable/ChangeInitVariable";
import PriceBitcoin from "../../components/priceBitcoin/PriceBitcoin";
import Notification from "../006-Notification/Notification";

const Header: React.FC = () => {

  const trendSmall = useSelector<AppRootStateType, mainTrendType>(
    store => store.app.bitcoinMainInformation.trendSmall)

  return (
    <div className={`
    ${cs.commonBody} 
    ${trendSmall === 'BULL' && cs.common__colorBull}
    ${trendSmall === 'BEAR' && cs.common__colorBear}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FLET' && cs.common__colorFlet}
    `}
    >
      <div className={`${cs.commonContainer} ${s.headerContainer}`}>

        <PriceBitcoin />

        <MainTrendIndicator name='Fast' variable='trendSmall'/>
        <MainTrendIndicator name='Slow' variable='trendBig'/>
        <MainTrendIndicator name='HA' variable='trendHA'/>

        <ChangeInitVariable name='Rate' variable='initSignalsValueRate' beforeSymbol='X ' afterSymbol=''/>
        <ChangeInitVariable name='TP' variable='initSignalsValueTP' beforeSymbol='' afterSymbol=' %'/>
        <ChangeInitVariable name='SL' variable='initSignalsValueSL' beforeSymbol='' afterSymbol=' %'/>

        <SearchActivator/>

        <Timer/>

        <Notification/>

      </div>
    </div>
  )
}

export default Header