import React, {useEffect} from "react";
import s from './Signals.module.css';
import cs from '../../common/styles/styles.module.css';
import {useDispatch, useSelector} from "react-redux";
import {resultSignalObjType, searchSignalsTC} from "../../store/appReducer";
import {AppRootStateType} from "../../store/store";
import SignalItem from "../../components/signalItem/SignalItem";

const Signals: React.FC = () => {

  const dispatch = useDispatch()

  const symbolNumber = useSelector<AppRootStateType, number>(
    state => state.app.symbolsInformation.symbolCurrentNumber)
  const symbolCurrent = useSelector<AppRootStateType, string>(
    state => state.app.symbolsInformation.symbolsList[symbolNumber])
  const symbolsList = useSelector<AppRootStateType, Array<string>>(
    state => state.app.symbolsInformation.symbolsList)

  const searchSignalsActive = useSelector<AppRootStateType, boolean>(
    state => state.app.appState.searchSignalsActive)

  const signalsList = useSelector<AppRootStateType, Array<resultSignalObjType>>(
    state => state.app.signalsInformation.signalsList)

  const signalsArray = signalsList.map((signal: resultSignalObjType) => {
    return <SignalItem key={signal.symbol} signal={signal} />
  })

  useEffect(() => {
    if (symbolsList.length && searchSignalsActive) {
      dispatch(searchSignalsTC(symbolCurrent, symbolNumber) as any)
    }
  }, [symbolsList, symbolNumber, searchSignalsActive])

  return (
    <div className={`${cs.commonBody} ${s.signalsBody}`}>
      <div className={`${cs.commonContainer} ${s.signalsContainer}`}>
        {signalsArray}
      </div>
    </div>
  )
}

export default Signals