import React, {useEffect} from 'react';
import {Provider, useDispatch, } from "react-redux";
import {store} from "./store/store";
import {BrowserRouter} from "react-router-dom";
import {setSymbolsListTC} from "./store/appReducer";
import Header from "./modules/001-Header/Header";
import Signals from "./modules/002-Signals/Signals";
import TradeHeader from "./modules/003-TradeHeader/TradeHeader";
import Trades from "./modules/004-Trades/Trades";
import Footer from "./modules/005-Footer/Footer";

const AppComponent: React.FC = () => {

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(setSymbolsListTC() as any)
  }, [])

  return (
    <>
      <Header />
      <Signals />
      <TradeHeader/>
      <Trades />
      <Footer />
    </>
  )
}

const App: React.FC = () => {

  return (
    <BrowserRouter>
      <Provider store={store}>
        <AppComponent/>
      </Provider>
    </BrowserRouter>
  )
}

export default App