import React, {useState} from 'react'
import s from "./Chart.module.css"
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  registerables,
} from 'chart.js';
import {Chart} from 'react-chartjs-2';

ChartJS.register(
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  ...registerables
);

type propsType = {
  dataPrice: Array<number>
  dataVolume: Array<number>
  children?: any
}

const Charter: React.FC<propsType> = props => {

  const {
    dataPrice,
    dataVolume,
    children
  } = props

  const labels = dataPrice.map((item, index) => {
    return index
  })

  const options = {
    responsive: true,
    interaction: {
      mode: 'index' as const,
      intersect: false,
    },
    stacked: false,
    plugins: {
      title: {
        display: true,
        text: 'Chart.js Line Chart - Multi Axis',
      },
    },
    scales: {
      y: {
        beginAtZero: false,
        type: 'linear' as const,
        display: true,
        position: 'left' as const,
      },
      y1: {
        type: 'linear' as const,
        display: true,
        position: 'right' as const,
        grid: {
          drawOnChartArea: false,
        },
      },
    },
  };

  const data = {
    labels,
    datasets: [
      {
        type: 'line' as const,
        label: 'Price',
        data: dataPrice,
        backgroundColor: 'rgb(130, 30, 30)',
        borderColor: 'rgb(130, 30, 30)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'bar' as const,
        label: 'Volume',
        data: dataVolume,
        backgroundColor: 'rgb(30, 130, 30)',
        borderColor: 'rgb(30, 130, 30)',
        yAxisID: 'y1',

      },
    ],
  };

  const [hoverChart, setHoverChart] = useState<boolean>(false)

  return (
    <>

      {children && <span
        onMouseEnter={() => setHoverChart(true)}
        onMouseLeave={() => setHoverChart(false)}
      >
        {children}
      </span>}

      {children
        ? (hoverChart &&
            <div
              className={`${s.chartBody}`}
              onMouseEnter={() => setHoverChart(true)}
              onMouseLeave={() => setHoverChart(false)}
            >
              <Chart type='bar' data={data} options={options}/>
            </div>
          )
        : <div
            className={`${s.chartBody}`}
          >
            <Chart type='bar' data={data} options={options}/>
          </div>
      }

    </>
  )
}

export default Charter