import React, {useState} from "react";
import {useSelector} from "react-redux";
import cs from "../../common/styles/styles.module.css";
import s from "./Notification.module.css";
import {AppRootStateType} from "../../store/store";
import {resultNotificationObjType} from "../../store/appReducer";
import NotificationItem from "../../components/notificationItem/NotificationItem";

const Notification: React.FC = () => {

  const notificationsList = useSelector<AppRootStateType, Array<resultNotificationObjType>>(
    state => state.app.notificationsInformation.notificationsList)

  const [show, setShow] = useState<boolean>(false)

  const notificationsArray = notificationsList.map((notificationItem: resultNotificationObjType) => {
    if (show) {
      return <NotificationItem key={notificationItem.id} notificationItem={notificationItem} show={show}/>
    } else {
      if (notificationItem.isActive) {
        return <NotificationItem key={notificationItem.id} notificationItem={notificationItem} show={show}/>
      }
    }
  })

  const onClick = () => {
    setShow(!show)
  }

  return (
    <>
      <div
        className={`${cs.commonHeaderButtonMain} ${s.buttonNotification}`}
        onClick={() => onClick()}
      >
        {show ? 'X' : ''}
      </div>

      {show && <div className={cs.backgroundModal} onClick={onClick}></div>}

      <div className={cs.notificationBody}>
        {notificationsArray}
      </div>

    </>
  )
}

export default Notification