import React from "react";
import s from './Trades.module.css';
import cs from '../../common/styles/styles.module.css';
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {resultTradesObjType} from "../../store/appReducer";
import TradeItem from "../../components/tradeItem/TradeItem";

const Trades: React.FC = () => {

  const tradesList = useSelector<AppRootStateType, Array<resultTradesObjType>>(
    state => state.app.tradesInformation.tradesList)

  const tradesArray = tradesList.map((tradeItem: resultTradesObjType) => {
    return <TradeItem key={tradeItem.symbol} tradeItem={tradeItem} />
  })

  return (
    <div className={`${cs.commonBody} ${s.tradesBody}`}>
      <div className={`${cs.commonContainer} ${s.tradesContainer}`}>
        {tradesArray}
      </div>
    </div>
  )
}

export default Trades