import React from "react";
import cs from "../../common/styles/styles.module.css";
import s from './ButtonActiveWindow.module.css'
import {useDispatch, useSelector} from "react-redux";
import {activeWindowType, setActiveWindowAC} from "../../store/appReducer";
import {AppRootStateType} from "../../store/store";


const ButtonActiveWindow: React.FC = () => {

  const dispatch = useDispatch()

  const activeWindow = useSelector<AppRootStateType, activeWindowType>(state => state.app.appState.activeWindow)

  const onClick = () => {
    dispatch(setActiveWindowAC(activeWindow === 'Trade' ? 'Position' : 'Trade'))
  }

  return (
    <div
      className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}
      onClick={onClick}
    >
      {activeWindow === 'Trade' ? 'Position' : 'Trade'}
    </div>
  )
}

export default ButtonActiveWindow