import React from "react";
import s from "./ArrowPriceIndicator.module.css";
import {signalUpPriceType} from "../../store/appReducer";

type propsType = {
  upPrice: signalUpPriceType
}

const ArrowPriceIndicator: React.FC<propsType> = props => {

  const {
    upPrice
  } = props

  return (
    <span className={s.signalArrow}>{upPrice === 1 && ''}{upPrice === -1 && ''}</span>
  )
}

export default ArrowPriceIndicator