import React, {useEffect, useState} from "react";
import s from './SignalItem.module.css';
import cs from '../../common/styles/styles.module.css';
import {
  addSignalToTradesListAC,
  deleteSignalAC,
  resultSignalObjType,
  setSymbolHoverInformationAC
} from "../../store/appReducer";
import {useDispatch} from "react-redux";
import PriceIndicator from "../priceIndicator/PriceIndicator";
import ArrowPriceIndicator from "../arrowPriceIndicator/ArrowPriceIndicator";
import Charter from "../chart/Chart";

type propsType = {
  signal: resultSignalObjType
}

const SignalItem: React.FC<propsType> = props => {

  const {
    signal
  } = props

  const dispatch = useDispatch()

  const [signalItem, setSignalItem] = useState<resultSignalObjType>(signal)
  const [signalHistoryDirection, setSignalHistoryDirection] = useState<string>('')

  const [hoverMain, setHoverMain] = useState<boolean>(false)
  const [hoverClose, setHoverClose] = useState<boolean>(false)

  const [clearTimerId, setClearTimerId] = useState<number>(0)

  const deleteSignal = () => {
    dispatch(deleteSignalAC(signalItem.symbol))
  }

  const addSignalToTradesList = () => {
    dispatch(addSignalToTradesListAC(signal))
  }

  const changeTimer = () => {
    setSignalItem({...signalItem, timeLife: signalItem.timeLife - 1})
    clearTimeout(clearTimerId)
  }

  //for generate string signals
  useEffect(() => {
    let resultSignalHistoryDirection = ''
    signal.signalHistoryDirection.map((item, index) => {
      if (index === 0) {
        resultSignalHistoryDirection = `${item}`
      } else {
        resultSignalHistoryDirection = resultSignalHistoryDirection + ` ${item.substr(0, 1)}`
      }
    })
    setSignalHistoryDirection(resultSignalHistoryDirection)

    setSignalItem(signal)

    clearTimeout(clearTimerId)
  }, [signal])

  //for timer
  useEffect(() => {
    if (signalItem.timeLife === 0) {
      dispatch(deleteSignalAC(signalItem.symbol))
    } else {
      const timer = setTimeout(changeTimer, 1000);
      setClearTimerId(+timer)
    }
  }, [signalItem.timeLife])

  //for sethoversymbolinformation
  useEffect(() => {
    dispatch(setSymbolHoverInformationAC(hoverMain ? signalItem.symbol : ''))
  }, [hoverMain])

  return (
    <>
      <div
        className={`${s.signalBody} ${signalItem.signalDirection === 'BUY' ? cs.common__colorBull : cs.common__colorBear}`}
        onMouseEnter={() => setHoverMain(true)}
        onMouseLeave={() => setHoverMain(false)}
        onDoubleClick={addSignalToTradesList}
      >
        <div className={`${s.signalRow}`}>
          <span>{signalItem.symbol.slice(0, -4)}</span>
          <PriceIndicator price={signalItem.priceLast} />
          <ArrowPriceIndicator upPrice={signalItem.upPrice} />
          <span
            className={cs.commonCloseButton}
            onMouseEnter={() => setHoverClose(true)}
            onMouseLeave={() => setHoverClose(false)}
            onClick={deleteSignal}
          >
          {hoverClose ? 'X' : signalItem.timeLife}
        </span>
        </div>
        <div className={`${s.signalRow}`}>
          <span>{signalItem.averageVolumeRelative}%</span>
          <span>{signalItem.priceDelta5mUSDT}%</span>
          <span>{signalItem.priceDelta5mBTC}%</span>
          <span>{signalItem.priceDelta1000m}%</span>
        </div>
        <div className={`${s.signalRow}`}>
          <span>{signalHistoryDirection}</span>
        </div>
        <div className={`${s.signalRow}`}>
          <span>{signalItem.signalDeltaTimer/60000 < 9999 ? `Last: ${signalItem.signalDeltaTimer/60000} mins ago` : `Never`}</span>
        </div>
      </div>
      {hoverMain &&
        <Charter dataPrice={signalItem.dataPrice} dataVolume={signalItem.dataVolume}/>
      }
    </>
  )
}

export default SignalItem