import React, {useEffect, useState} from 'react'
import {deleteNotificationAC, resultNotificationObjType, setNoActiveNotificationAC} from "../../store/appReducer";
import s from './NotificationItem.module.css'
import {useDispatch} from "react-redux";
import cs from "../../common/styles/styles.module.css";

type propsType = {
  notificationItem: resultNotificationObjType
  show: boolean
}

const NotificationItem: React.FC<propsType> = props => {

  const {
    notificationItem,
    show
  } = props

  const dispatch = useDispatch()

  const [minsAgo, setMinsAgo] = useState<number>(0)

  const onClose = () => {
    if (show) {
      dispatch(deleteNotificationAC(notificationItem.id))
    } else {
      dispatch(setNoActiveNotificationAC(notificationItem.id))
    }
  }

  useEffect(() => {
    let now = Date.now()
    setMinsAgo(Math.floor((now - notificationItem.initTime)/60000))
  }, [show])

  useEffect(() => {
    setTimeout(() => {
      dispatch(setNoActiveNotificationAC(notificationItem.id))
    }, 10000)
  }, [])

  return (
    <div className={`${s.notificationBody}`}>
      <div className={`${s.notificationRow}`}>
        <span>{notificationItem.title}</span>
        <span
          className={cs.commonCloseButton}
          onClick={onClose}
        >
          X
        </span>
      </div>
      <div className={`${s.notificationRow}`}>
        <span>{notificationItem.description}</span>
      </div>
      <div className={`${s.notificationRow}`}>
        <span>{minsAgo < 1 ? 'Now' : `${minsAgo} mins ago`}</span>
      </div>
    </div>
  )
}

export default NotificationItem