import React from 'react';
import s from "./TradeHeader.module.css";
import cs from '../../common/styles/styles.module.css';
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {mainTrendType} from "../../store/appReducer";

const TradeHeader: React.FC = () => {

  const trendSmall = useSelector<AppRootStateType, mainTrendType>(
    store => store.app.bitcoinMainInformation.trendSmall)

  return (
    <div className={`
    ${cs.commonBody} 
    ${trendSmall === 'BULL' && cs.common__colorBull}
    ${trendSmall === 'BEAR' && cs.common__colorBear}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FL ' && cs.common__colorFlet}
    ${trendSmall === 'FLET' && cs.common__colorFlet}
    `}
    >
      <div className={`${cs.commonContainer} ${s.tableHeaderContainer}`}>

        <span>Coin:</span>
        <span>Open $:</span>
        <span>Now $:</span>
        <span>Up:</span>

        <span>Vol:</span>

        <span>ROE %:</span>
        <span>MAX %:</span>
        {/*<span>TP $:</span>*/}
        <span>TP %:</span>
        {/*<span>SL $:</span>*/}
        <span>SL %:</span>
        <span>Alert:</span>
        <span>Chart:</span>
        <span>Close:</span>

      </div>
    </div>
  )
}

export default TradeHeader