import React, {useState} from 'react';
import s from "./ChangeInitVariable.module.css";
import cs from '../../common/styles/styles.module.css';
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {setInitVariableValueAC, variableChangeInitVariablesType} from "../../store/appReducer";

type propsType = {
  name: string
  variable: variableChangeInitVariablesType
  beforeSymbol: string
  afterSymbol: string
}

const ChangeInitVariable: React.FC<propsType> = props => {

  const {
    name,
    variable,
    beforeSymbol,
    afterSymbol
  } = props

  const dispatch = useDispatch()

  const value = useSelector<AppRootStateType, number>(state => state.app.initVariables[`${variable}`])

  const [startValue,] = useState<number>(value)

  const changeValue = (newValue: number) => {
    if (newValue > 0 && newValue < 101) {
      dispatch(setInitVariableValueAC(variable, newValue))
    }
  }

  return (
    <div className={cs.commonFlexBody}>
      <span>{name}:</span>
      <span
        className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}
        onClick={() => changeValue(startValue)}
      >
        {beforeSymbol}{value}{afterSymbol}
      </span>
      <div className={s.tradeButtonsContainer}>
        <span className={s.headerButtonSmall} onClick={() => changeValue(value+1)}>{}</span>
        <span className={s.headerButtonSmall} onClick={() => changeValue(value-1)}>{}</span>
      </div>
    </div>
  )
}

export default ChangeInitVariable