import React, {useEffect, useState} from "react";
import s from './TradeItem.module.css';
import cs from '../../common/styles/styles.module.css';
import {
  deleteTradeAC, dropHandlerTradeItemAC,
  resultTradesObjType, setAlertTradeStatusAC, setCurrentDropTradeAC, setNewNotificationAC, setSymbolHoverInformationAC,
} from "../../store/appReducer";
import {useDispatch, useSelector} from "react-redux";
import PriceIndicator from "../priceIndicator/PriceIndicator";
import ArrowPriceIndicator from "../arrowPriceIndicator/ArrowPriceIndicator";
import Charter from "../chart/Chart";
import {AppRootStateType} from "../../store/store";

type propsType = {
  tradeItem: resultTradesObjType
}

const TradeItem: React.FC<propsType> = props => {

  const {
    tradeItem
  } = props

  const dispatch = useDispatch()

  const currentDropSymbol = useSelector<AppRootStateType, undefined | string>(state => state.app.tradesInformation.currentDropTrade?.symbol)

  const [hoverMain, setHoverMain] = useState<boolean>(false)
  const [hoverDrag, setHoverDrag] = useState<boolean>(false)

  const [alertsActive, setAlertsActive] = useState<boolean>(true)

  const deleteTrade = () => {
    dispatch(deleteTradeAC(tradeItem.symbol))
  }

  //'TP' | 'SL' | 'RS' | 'DS'
  useEffect(() => {
    if (alertsActive) {
      if (tradeItem.alertTrade === 'TP') {
        dispatch(setNewNotificationAC(
          `Take Profit - ${tradeItem.symbol}`,
          `${tradeItem.priceLast}`,
          'TP'))
      }

      if (tradeItem.alertTrade === 'SL') {
        dispatch(setNewNotificationAC(
          `Stop Loss - ${tradeItem.symbol}`,
          `Close: ${tradeItem.tradeDirection} - ${tradeItem.priceLast}`,
          'SL'))
      }

      if (tradeItem.alertTrade === 'RS') {
        dispatch(setNewNotificationAC(
          `Reverse Signal - ${tradeItem.symbol}`,
          `Close: ${tradeItem.tradeDirection} - ${tradeItem.priceLast}`,
          'RS'))
      }

      if (tradeItem.alertTrade === 'DS') {
        dispatch(setNewNotificationAC(
          `Direction Signal - ${tradeItem.symbol}`,
          `New signal in trade: ${tradeItem.tradeDirection} - ${tradeItem.priceLast}`,
          'DS'))
        setTimeout(() => {
          dispatch(setAlertTradeStatusAC(tradeItem.symbol, null))
        }, 20000)
      }
    }
  }, [tradeItem])

  function dragStartHandler() {
    //start card
    dispatch(setCurrentDropTradeAC(tradeItem))
  }

  function dragLeaveHandler() {
    setHoverDrag(false)
  }

  function dragEnd() {
    dispatch(setCurrentDropTradeAC(null))
  }

  function dragOverHandler(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault()
    if (currentDropSymbol !== tradeItem.symbol) {
      setHoverDrag(true)
    }
  }

  function dropHandler(e: React.DragEvent<HTMLDivElement>) {
    //drop card
    e.preventDefault()
    if (currentDropSymbol !== tradeItem.symbol) {
      dispatch(dropHandlerTradeItemAC(tradeItem))
    }
  }

  useEffect(() => {
    setHoverDrag(false)
    //dispatch(setCurrentDropTradeAC(null))
  }, [currentDropSymbol])

  //for sethoversymbolinformation
  useEffect(() => {
    dispatch(setSymbolHoverInformationAC(hoverMain ? tradeItem.symbol : ''))
  }, [hoverMain])

  return (
    <>
      <div
        className={`
          ${s.tradeBody} 
          ${tradeItem.tradeDirection === 'BUY' ? cs.common__colorBull : cs.common__colorBear}
        `}
        onMouseEnter={() => setHoverMain(true)}
        onMouseLeave={() => setHoverMain(false)}

        draggable={true}
        onDragStart={() => dragStartHandler()}
        onDragLeave={() => dragLeaveHandler()}
        onDragEnd={() => dragEnd()}
        onDragOver={(e) => dragOverHandler(e)}
        onDrop={(e) => dropHandler(e)}
      >

        <span>{tradeItem.symbol.slice(0, -4)}</span>
        <PriceIndicator price={tradeItem.priceOpen}/>
        <PriceIndicator price={tradeItem.priceLast}/>
        <ArrowPriceIndicator upPrice={tradeItem.upPrice}/>
        <span>{tradeItem.averageVolumeRelative} %</span>
        <span>{tradeItem.percentLast} %</span>
        <span>{tradeItem.percentMax} %</span>
        {/*<PriceIndicator price={tradeItem.priceTP}/>*/}
        <span>{tradeItem.percentTP} %</span>
        {/*<PriceIndicator price={tradeItem.priceSL}/>*/}
        <span>{-1 * tradeItem.percentSL} %</span>

        <span
          className={cs.commonCloseButton}
          onClick={() => setAlertsActive(!alertsActive)}
        >
          {alertsActive ? tradeItem.alertTrade : 'X'}
        </span>

        <Charter dataPrice={tradeItem.dataPrice} dataVolume={tradeItem.dataVolume}>
          <span
            className={`${cs.commonCloseButton} ${s.chartIcon}`}
          >
            
          </span>
        </Charter>

        <span
          className={cs.commonCloseButton}
          onClick={deleteTrade}
        >
          X
        </span>

      </div>

      {hoverDrag &&
        <div className={s.trade__dropPlace}>{}</div>
      }

    </>

  )
}

export default TradeItem