import axios from "axios";

const instanceAPI = axios.create({
  baseURL: 'https://api.binance.com/api/v3/',
})

const instanceDAPI = axios.create({
  baseURL: 'https://dapi.binance.com/dapi/v1/',
})


export type serverTimeResponseType = {
  serverTime: number
}

export type symbolResponseType = {
  symbol: string
  pair: string
  bidPrice: number
  bidQty: number
  askPrice: number
  askQty: number
  time: number
}


export const binanceAPI = {
  getServerTime() {
    return instanceAPI.get<serverTimeResponseType>(`time`)
      .then(r => r.data)
  },

  getKlines(config: object) {
    return instanceAPI.get(`klines`, {params: config})
      .then(r => r.data)
  }
}

export const binanceDAPI = {
  getSymbolsList() {
    return instanceDAPI.get<Array<symbolResponseType>>(`ticker/bookTicker`)
      .then(r => r.data)
  },
}