import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {setSearchSignalsActiveAC} from "../../store/appReducer";
import cs from "../../common/styles/styles.module.css";
import s from './SearchActivator.module.css'

const SearchActivator: React.FC = () => {

  const dispatch = useDispatch()

  const searchSignalsActive = useSelector<AppRootStateType, boolean>(
    state => state.app.appState.searchSignalsActive)

  const onClick = () => {
    dispatch(setSearchSignalsActiveAC())
  }

  return (
    <div className={`${cs.commonHeaderButtonMain} ${s.mainButton}`} onClick={onClick}>
      {searchSignalsActive ? 'ON' : 'OFF'}
    </div>
  )
}

export default SearchActivator