import React, {useEffect, useState} from 'react';
import {setBitcoinMainInformationTC} from "../../store/appReducer";
import {useDispatch} from "react-redux";
import cs from "../../common/styles/styles.module.css";
import s from "./Timer.module.css";

let timeout: any

const Timer: React.FC = () => {

  const dispatch = useDispatch()

  const [timer, setTimer] = useState<number>(0)

  useEffect(() => {
    if (timer === 0) {
      setTimer(60)
      dispatch(setBitcoinMainInformationTC() as any)
    } else {
      timeout = setTimeout(() => {
        setTimer(timer - 1)
      }, 1000)
    }
  }, [timer])

  const onChangeTimer = () => {
    if (timer !== 0 && timer !== 60) {
      clearTimeout(timeout)
      setTimer(0)
    }
  }

  return (
    <div
      className={`${cs.commonHeaderButtonMain} ${s.mainButton}`}
      onClick={onChangeTimer}
    >
      {timer} s
    </div>
  )
}

export default Timer