export const appReducer = (state: InitStateType = initState, action: actionType): InitStateType => {
  switch (action.type) {

    case 'app/setServerTime': {
      let copyState = {...state}

      copyState.appState = {...copyState.appState, serverTime: action.serverTimeResponse.serverTime}

      return copyState
    }

    case 'app/setSymbolsList': {
      let copyState = {...state}

      copyState.symbolsInformation = {...copyState.symbolsInformation}
      copyState.symbolsInformation.symbolsList = []


      action.symbolsResponseList.map((symbolResponseItem: symbolResponseType) => {
        let symbol = `${symbolResponseItem.pair}T`
        if (!copyState.symbolsInformation.symbolsList.includes(symbol)) {
          copyState.symbolsInformation.symbolsList.push(symbol)

          copyState.signalsInformation.signalsHistoryDirection[`${symbol}`] = []
          copyState.signalsInformation.signalsHistoryTime[`${symbol}`] = 0

          copyState.tradesInformation.tradesHistoryPositiveBuy[`${symbol}`] = 0
          copyState.tradesInformation.tradesHistoryPositiveSell[`${symbol}`] = 0
          copyState.tradesInformation.tradesHistoryNegativeBuy[`${symbol}`] = 0
          copyState.tradesInformation.tradesHistoryNegativeSell[`${symbol}`] = 0

        }
      })

      copyState.symbolsInformation.symbolsCount = copyState.symbolsInformation.symbolsList.length
      copyState.symbolsInformation.symbolCurrentNumber = 0

      return copyState
    }

    case 'app/setBitcoinMainInformation': {
      let copyState = {...state}

      let sumAverage900m = 0
      let sumAverage90m = 0
      let sumAverage15m = 0

      let summaryVolume900m = 0
      let summaryVolume5m = 0

      let dataPriceBTC = []
      let dataVolumeBTC = []

      for (let i = 0; i < action.dataMain.length; i++) {
        sumAverage900m = sumAverage900m + +action.dataMain[i][4]
        summaryVolume900m = summaryVolume900m + +action.dataMain[i][5]

        if (i > 809) {
          sumAverage90m = sumAverage90m + +action.dataMain[i][4]
        }

        if (i > 884) {
          sumAverage15m = sumAverage15m + +action.dataMain[i][4]
        }

        if (i > 894) {
          summaryVolume5m = summaryVolume5m + +action.dataMain[i][5]
        }

        if (i > 699) {
          dataPriceBTC.push(+action.dataMain[i][4])
          dataVolumeBTC.push(+action.dataMain[i][5])
        }
      }

      //sredniy ob'em za 900 min
      const averageVolume900m = summaryVolume900m / 900

      //sredniy ob'em za 5 min
      const averageVolume5m = summaryVolume5m / 5

      //izmenenie ob'ema - naskolko srednii ob'em za 5 min prevoshodit sredniy ob'em za 900 min
      const averageVolumeRelative = (averageVolume5m - averageVolume900m) / averageVolume900m

      copyState.bitcoinMainInformation = {...copyState.bitcoinMainInformation}

      //berem staroe znachenie
      let oldPriceBtc = copyState.bitcoinMainInformation.priceCurrent

      copyState.bitcoinMainInformation.priceCurrent = Math.round(action.dataMain[899][4])

      if (copyState.bitcoinMainInformation.priceCurrent > oldPriceBtc) {
        copyState.bitcoinMainInformation.upPrice = 1
      }
      if (copyState.bitcoinMainInformation.priceCurrent < oldPriceBtc) {
        copyState.bitcoinMainInformation.upPrice = -1
      }
      if (copyState.bitcoinMainInformation.priceCurrent === oldPriceBtc) {
        copyState.bitcoinMainInformation.upPrice = 0
      }

      copyState.bitcoinMainInformation.priceAverage900m = Math.round(sumAverage900m / 900)
      copyState.bitcoinMainInformation.priceAverage90m = Math.round(sumAverage90m / 90)
      copyState.bitcoinMainInformation.priceAverage15m = Math.round(sumAverage15m / 15)


      copyState.bitcoinMainInformation.averageVolumeRelative = Math.round(averageVolumeRelative * 100)

      copyState.bitcoinMainInformation.dataPriceBTC = dataPriceBTC
      copyState.bitcoinMainInformation.dataVolumeBTC = dataVolumeBTC

      if (copyState.bitcoinMainInformation.priceAverage15m > copyState.bitcoinMainInformation.priceAverage90m) {
        if (copyState.bitcoinMainInformation.priceCurrent > copyState.bitcoinMainInformation.priceAverage15m) {
          copyState.bitcoinMainInformation.trendSmall = 'BULL'
        } else {
          copyState.bitcoinMainInformation.trendSmall = 'FL '
        }
      } else {
        if (copyState.bitcoinMainInformation.priceCurrent < copyState.bitcoinMainInformation.priceAverage15m) {
          copyState.bitcoinMainInformation.trendSmall = 'BEAR'
        } else {
          copyState.bitcoinMainInformation.trendSmall = 'FL '
        }
      }

      if (copyState.bitcoinMainInformation.priceAverage90m > copyState.bitcoinMainInformation.priceAverage900m) {
        if (copyState.bitcoinMainInformation.priceAverage15m > copyState.bitcoinMainInformation.priceAverage90m) {
          copyState.bitcoinMainInformation.trendBig = 'BULL'
        } else {
          copyState.bitcoinMainInformation.trendBig = 'FL '
        }
      } else {
        if (copyState.bitcoinMainInformation.priceAverage15m < copyState.bitcoinMainInformation.priceAverage90m) {
          copyState.bitcoinMainInformation.trendBig = 'BEAR'
        } else {
          copyState.bitcoinMainInformation.trendBig = 'FL '
        }
      }

      const bitcoinHaPriceClose = Math.round((+action.dataHA[1][1] + +action.dataHA[1][2] + +action.dataHA[1][3] + +action.dataHA[1][4]) / 4)
      const bitcoinHaPriceOpen = Math.round((+action.dataHA[0][1] + +action.dataHA[0][4]) / 2)

      const bitcoinHaPriceDelta = bitcoinHaPriceClose / bitcoinHaPriceOpen

      if (bitcoinHaPriceDelta > copyState.initVariables.bitcoinHATrendDeltaValue) {
        copyState.bitcoinMainInformation.trendHA = 'BULL'
      } else {
        if (bitcoinHaPriceDelta < 1 / copyState.initVariables.bitcoinHATrendDeltaValue) {
          copyState.bitcoinMainInformation.trendHA = 'BEAR'
        } else {
          copyState.bitcoinMainInformation.trendHA = 'FLET'
        }
      }

      return copyState
    }

    case 'app/setSearchSignalsActive': {
      let copyState = {...state}

      copyState.appState = {...copyState.appState, searchSignalsActive: !copyState.appState.searchSignalsActive}

      return copyState
    }

    case 'app/setInitVariableValue': {
      let copyState = {...state}

      copyState.initVariables = {...state.initVariables}
      copyState.initVariables[`${action.changeInitVariable}`] = action.newValue

      return copyState
    }

    case 'app/searchSignals': {
      let copyState = {...state}

      let plus1 = 0 //Absolutnui ob'em
      let plus2 = 0 //Otnositelnui ob'em
      let plus3 = 0 //izmenenie cenu za 5 min v USDT vushe minimalnogo
      let plus4 = 0 //izmenenie cenu za 5 min v USDT nije maksimalnogo
      let plus5 = 0 //izmenenie cenu za 5 min v BTC vushe minimalnogo
      let plus6 = 0 //izmenenie cenu za 5 min v BTC nije maksimalnogo

      //poslednyaya cena USDT
      const priceLastUSDT = +action.symbolDataUSDT[999][4]

      //izmenenie cenu za 1000min USDT
      const priceDelta1000mUSDT = (+action.symbolDataUSDT[999][4] - +action.symbolDataUSDT[0][1]) / +action.symbolDataUSDT[0][1]

      //izmenenie cenu za 5min v USDT
      const priceDelta5mUSDT = (+action.symbolDataUSDT[999][4] - +action.symbolDataUSDT[994][1]) / +action.symbolDataUSDT[994][1]

      //izmenenie cenu za 5min v BTC
      const priceDelta5mBTC = (+action.symbolDataBTC[4][4] - +action.symbolDataBTC[0][1]) / +action.symbolDataBTC[0][1]

      let summaryVolume1000m = 0
      let summaryVolume5m = 0

      let dataPrice = []
      let dataVolume = []

      for (let i = 0; i < action.symbolDataUSDT.length; i++) {
        summaryVolume1000m = summaryVolume1000m + +action.symbolDataUSDT[i][5]

        if (i > 799) {
          dataPrice.push(+action.symbolDataUSDT[i][4])
          dataVolume.push(+action.symbolDataUSDT[i][5])
        }

        if (i > 994) {
          summaryVolume5m = summaryVolume5m + +action.symbolDataUSDT[i][5]
        }
      }

      //sredniy ob'em za 1000 min
      const averageVolume1000m = summaryVolume1000m / 1000

      //sredniy ob'em za 5 min
      const averageVolume5m = summaryVolume5m / 5

      //izmenenie ob'ema - naskolko srednii ob'em za 5 min prevoshodit sredniy ob'em za 1000 min
      const averageVolumeRelative = (averageVolume5m - averageVolume1000m) / averageVolume1000m

      //Usloviya plusov

      //Absolutnui ob'em - esli ob'em vushe znacheniya
      if (averageVolume1000m > copyState.initVariables.limitAverageVolumeAbsolute1000m) {
        plus1 = 1
      }

      //Otnositelnui ob'em - esli izmenenie ob'ema vushe znacheniya
      if (averageVolumeRelative > copyState.initVariables.limitAverageVolumeRelative) {
        plus2 = 1
      }

      //izmenenie cenu za 5 min v USDT vushe minimalnogo
      if (priceDelta5mUSDT > copyState.initVariables.limitMinPriceDelta5mUSDT) {
        plus3 = 20

        //izmenenie cenu za 5 min v USDT nije maksimalnogo
        if (priceDelta5mUSDT < copyState.initVariables.limitMaxPriceDelta5mUSDT) {
          plus4 = 200
        }

      }
      if (priceDelta5mUSDT < (-1 * copyState.initVariables.limitMinPriceDelta5mUSDT)) {
        plus3 = 10

        //izmenenie cenu za 5 min v USDT nije maksimalnogo
        if (priceDelta5mUSDT > (-1 * copyState.initVariables.limitMaxPriceDelta5mUSDT)) {
          plus4 = 100
        }

      }

      //izmenenie cenu za 5 min v BTC vushe minimalnogo
      if (priceDelta5mBTC > copyState.initVariables.limitMinPriceDelta5mBTC) {
        plus5 = 2000

        //izmenenie cenu za 5 min v BTC nije maksimalnogo
        if (priceDelta5mBTC < copyState.initVariables.limitMaxPriceDelta5mBTC) {
          plus6 = 20000
        }
      }
      if (priceDelta5mBTC < (-1 * copyState.initVariables.limitMinPriceDelta5mBTC)) {
        plus5 = 1000

        //izmenenie cenu za 5 min v BTC nije maksimalnogo
        if (priceDelta5mBTC > (-1 * copyState.initVariables.limitMaxPriceDelta5mBTC)) {
          plus6 = 10000
        }
      }

      let plus = plus1 + plus2 + plus3 + plus4 + plus5 + plus6

      let isSignal: signalIsSignalType = 'NO'

      if (plus === 22222) { //222
        isSignal = 'BUY'
      }
      if (plus === 11112) { //112
        isSignal = 'SELL'
      }

      //sozdanie spiska signalov tolko symbols
      let signalSymbolsList = copyState.signalsInformation.signalsList.map(signalItem => signalItem.symbol)

      //sozdanie spiska traidov tolko symbols
      let tradesSymbolsList = copyState.tradesInformation.tradesList.map(tradeItem => tradeItem.symbol)

      //esli EST' SIGNAL
      if (isSignal !== 'NO') {

        //Perezapisuvanie istorii napravleniya signalov
        //schituvaem starui spisok napravlenii po pare
        let signalHistoryDirection = [...copyState.signalsInformation.signalsHistoryDirection[`${action.symbolCurrent}`]]

        //ochichaem poslednii esli perepolnen
        if (signalHistoryDirection.length > 8) {
          signalHistoryDirection.pop()
        }

        //dobalyaem signal v nachalo
        signalHistoryDirection.unshift(isSignal)

        //zapisuvaem obnolennui spisok
        copyState.signalsInformation.signalsHistoryDirection = {...copyState.signalsInformation.signalsHistoryDirection}
        copyState.signalsInformation.signalsHistoryDirection[`${action.symbolCurrent}`] = signalHistoryDirection

        //Perezapisuvanie istorii vremeni signalov
        //schituvanie starogo vremeni signala
        let lastSignalTime = copyState.signalsInformation.signalsHistoryTime[`${action.symbolCurrent}`]

        //delta starogo signala vremeni ot seychas
        let signalDeltaTimer = +action.symbolDataUSDT[999][6] - lastSignalTime

        //zapis' novogo vremeni signala
        copyState.signalsInformation.signalsHistoryTime = {...copyState.signalsInformation.signalsHistoryTime}
        copyState.signalsInformation.signalsHistoryTime[`${action.symbolCurrent}`] = +action.symbolDataUSDT[999][6]


        if (tradesSymbolsList.includes(action.symbolCurrent)) { //esli signal uje est' v trade

          copyState.tradesInformation.tradesList = TradeListGenerator(action.symbolCurrent,
            priceLastUSDT,
            averageVolumeRelative,
            isSignal,
            copyState.tradesInformation.tradesList,
            dataPrice,
            dataVolume)

        } else { //esli signala net v trade

          //sozdanie rezultiruechego obj dlya dobaleniya v spisok signalov
          let resultSignalObj: resultSignalObjType = {
            symbol: action.symbolCurrent,
            priceLast: Math.round(priceLastUSDT * 10000) / 10000,
            priceDelta1000m: Math.round(priceDelta1000mUSDT * 1000) / 10,
            priceDelta5mUSDT: Math.round(priceDelta5mUSDT * 1000) / 10,
            priceDelta5mBTC: Math.round(priceDelta5mBTC * 1000) / 10,
            averageVolumeRelative: Math.round(averageVolumeRelative * 100),
            signalHistoryDirection: signalHistoryDirection,
            signalDeltaTimer: signalDeltaTimer,
            timeLife: 100, //timelife for signals
            signalDirection: isSignal,
            upPrice: 0,
            dataPrice: dataPrice,
            dataVolume: dataVolume,
          }

          //pereopredelenie lista signalov
          let signalsList: Array<resultSignalObjType> = []

          //samo obnovlenie (if) ili dobavlenie (else) signala v spiske signalov
          if (signalSymbolsList.includes(action.symbolCurrent)) {
            signalsList = copyState.signalsInformation.signalsList.map((signalItem: resultSignalObjType) => {
              if (signalItem.symbol === resultSignalObj.symbol) {

                let upPrice = 0 as signalUpPriceType
                if (resultSignalObj.priceLast > signalItem.priceLast) {
                  upPrice = 1 as signalUpPriceType
                }
                if (resultSignalObj.priceLast < signalItem.priceLast) {
                  upPrice = -1 as signalUpPriceType
                }
                return {...resultSignalObj, upPrice: upPrice}
              } else {
                return signalItem
              }
            })
          } else {
            signalsList = [...copyState.signalsInformation.signalsList, resultSignalObj]
          }

          //konechnaya zapis'
          copyState.signalsInformation.signalsList = signalsList

          //zapisuvaem kolichestvo signalov
          copyState.signalsInformation.signalsCount = copyState.signalsInformation.signalsList.length

          //daem signal
          const notif = new Notification(
            `New signal`,
            {
              tag: 'TAG',
              body: `${action.symbolCurrent} - ${isSignal}`
            }
          )
        }
      } else { //esli net signala zdes' nyjno prosto obnovit' cenu trade

        copyState.tradesInformation.tradesList = TradeListGenerator(action.symbolCurrent,
          priceLastUSDT,
          averageVolumeRelative,
          isSignal,
          copyState.tradesInformation.tradesList,
          dataPrice,
          dataVolume)
      }

      //rabota iteratora
      if (action.symbolNumber < copyState.symbolsInformation.symbolsCount - 1) {
        copyState.symbolsInformation.symbolCurrentNumber = action.symbolNumber + 1
        //copyState.ticketsNumber = 1
      } else {
        copyState.symbolsInformation.symbolCurrentNumber = 0
      }

      return copyState
    }

    case 'app/deleteSignal': {
      let copyState = {...state}

      copyState.signalsInformation.signalsList = copyState.signalsInformation.signalsList.filter(
        signal => signal.symbol !== action.symbol)

      //zapisuvaem kolichestvo signalov
      copyState.signalsInformation.signalsCount = copyState.signalsInformation.signalsList.length

      return copyState
    }

    case 'app/addSignalToTradesList': {
      let copyState = {...state}

      let priceTP = action.signal.signalDirection === 'BUY'
        ? action.signal.priceLast + (action.signal.priceLast * state.initVariables.initSignalsValueTP) / (100 * state.initVariables.initSignalsValueRate)
        : action.signal.priceLast - (action.signal.priceLast * state.initVariables.initSignalsValueTP) / (100 * state.initVariables.initSignalsValueRate)

      let priceSL = action.signal.signalDirection === 'BUY'
        ? action.signal.priceLast - (action.signal.priceLast * state.initVariables.initSignalsValueSL) / (100 * state.initVariables.initSignalsValueRate)
        : action.signal.priceLast + (action.signal.priceLast * state.initVariables.initSignalsValueSL) / (100 * state.initVariables.initSignalsValueRate)

      priceTP = Math.round(priceTP * 10000) / 10000
      priceSL = Math.round(priceSL * 10000) / 10000

      //sozdanie rezultiruechego obj dlya dobaleniya v spisok tradov
      let resultTradeObj: resultTradesObjType = {
        symbol: action.signal.symbol, //symbol
        priceOpen: action.signal.priceLast, //cena otkrutiya

        priceLast: action.signal.priceLast, //poslednyaya cena
        percentLast: 0, //poslednyaya cena v procentah

        averageVolumeRelative: action.signal.averageVolumeRelative,

        priceTP: priceTP, //cena TP
        percentTP: state.initVariables.initSignalsValueTP, //cena TP v procentah

        priceSL: priceSL, //cena SL
        percentSL: state.initVariables.initSignalsValueSL, //cena SL v procentah

        percentMax: 0, //cena Max v procentah

        initValueTP: state.initVariables.initSignalsValueTP, //initValueTP
        initValueSL: state.initVariables.initSignalsValueSL, //initValueSL
        initValueRate: state.initVariables.initSignalsValueRate, //initValueRate
        initValueLevelBU: state.initVariables.initSignalsValueLevelBU, //initValueLevelBU
        initValueDeltaPrice: state.initVariables.initSignalsValueDeltaPrice, //initValueDeltaPrice

        tradeDirection: action.signal.signalDirection, //trend pri otkrutii trenda
        upPrice: 0, //napravlenie cenu s poslednego obnovleniya
        alertTrade: null, //alert treida

        dataPrice: action.signal.dataPrice, //dlya grafica cenu
        dataVolume: action.signal.dataVolume, //dlya grafica ob'ema
      }

      //dobavleniya trade v spisok tradov
      copyState.tradesInformation.tradesList = [...copyState.tradesInformation.tradesList, resultTradeObj]

      //udalenie signala
      copyState.signalsInformation.signalsList = copyState.signalsInformation.signalsList.filter(
        signal => signal.symbol !== action.signal.symbol)

      //zapisuvaem kolichestvo signalov
      copyState.signalsInformation.signalsCount = copyState.signalsInformation.signalsList.length

      return copyState
    }

    case 'app/deleteTrade': {
      let copyState = {...state}

      copyState.tradesInformation.tradesList = copyState.tradesInformation.tradesList.filter(
        (trade) => {

          if (trade.symbol !== action.symbol) {
            return trade
          } else {
            if (trade.tradeDirection === 'BUY') {
              if (trade.percentLast > 0) {
                copyState.tradesInformation.tradesHistoryPositiveBuy[`${trade.symbol}`] = copyState.tradesInformation.tradesHistoryPositiveBuy[`${trade.symbol}`] + 1
              } else {
                copyState.tradesInformation.tradesHistoryNegativeBuy[`${trade.symbol}`] = copyState.tradesInformation.tradesHistoryNegativeBuy[`${trade.symbol}`] + 1
              }
            } else {
              if (trade.percentLast > 0) {
                copyState.tradesInformation.tradesHistoryPositiveSell[`${trade.symbol}`] = copyState.tradesInformation.tradesHistoryPositiveSell[`${trade.symbol}`] + 1
              } else {
                copyState.tradesInformation.tradesHistoryNegativeSell[`${trade.symbol}`] = copyState.tradesInformation.tradesHistoryNegativeSell[`${trade.symbol}`] + 1
              }
            }
            copyState.symbolsInformation.symbolHoverInformation = ''
          }
        })

      return copyState
    }

    case 'app/loadDataFromLocalStorage': {
      let copyState = {...state}

      let saveStr = localStorage.getItem("saveObj")
      if (saveStr) {
        let saveObj: InitStateType = JSON.parse(saveStr)
        copyState.initVariables = saveObj.initVariables

        copyState.signalsInformation.signalsHistoryDirection = saveObj.signalsInformation.signalsHistoryDirection
        copyState.signalsInformation.signalsHistoryTime = saveObj.signalsInformation.signalsHistoryTime

        copyState.tradesInformation.tradesList = saveObj.tradesInformation.tradesList
        copyState.tradesInformation.tradesHistoryPositiveBuy = saveObj.tradesInformation.tradesHistoryPositiveBuy
        copyState.tradesInformation.tradesHistoryPositiveSell = saveObj.tradesInformation.tradesHistoryPositiveSell
        copyState.tradesInformation.tradesHistoryNegativeBuy = saveObj.tradesInformation.tradesHistoryNegativeBuy
        copyState.tradesInformation.tradesHistoryNegativeSell = saveObj.tradesInformation.tradesHistoryNegativeSell

        copyState.notificationsInformation.notificationsList = saveObj.notificationsInformation.notificationsList
        copyState.notificationsInformation.notificationsNextNumber = saveObj.notificationsInformation.notificationsNextNumber
      }

      return copyState
    }

    case 'app/saveDataToLocalStorage': {
      let copyState = {...state}

      let saveObj = {
        initVariables: {...state.initVariables},

        signalsInformation: {
          signalsHistoryDirection: state.signalsInformation.signalsHistoryDirection,
          signalsHistoryTime: state.signalsInformation.signalsHistoryTime,
        },

        tradesInformation: {
          tradesList: state.tradesInformation.tradesList,
          tradesHistoryPositiveBuy: state.tradesInformation.tradesHistoryPositiveBuy,
          tradesHistoryPositiveSell: state.tradesInformation.tradesHistoryPositiveSell,
          tradesHistoryNegativeBuy: state.tradesInformation.tradesHistoryNegativeBuy,
          tradesHistoryNegativeSell: state.tradesInformation.tradesHistoryNegativeSell,
        },

        notificationsInformation: {
          notificationsList: copyState.notificationsInformation.notificationsList,
          notificationsNextNumber: copyState.notificationsInformation.notificationsNextNumber,
        }
      }

      localStorage.setItem('saveObj', JSON.stringify(saveObj))



      return copyState
    }

    case 'app/setSymbolHoverInformation': {
      let copyState = {...state}

      copyState.symbolsInformation.symbolHoverInformation = action.symbolHoverInformation

      return copyState
    }

    case 'app/setAlertTradeStatus': {
      let copyState = {...state}

      copyState.tradesInformation.tradesList = copyState.tradesInformation.tradesList.map((trade: resultTradesObjType) => {
        if (trade.symbol === action.symbol) {
          trade.alertTrade = action.alert
        }
        return trade
      })

      return copyState
    }

    case 'app/setActiveWindow': {
      let copyState = {...state}

      copyState.appState.activeWindow = action.activeWindow

      return copyState
    }

    case 'app/setNewNotification': {
      let copyState = {...state}

      let newArray = [...copyState.notificationsInformation.notificationsList]

      const resultNotificationObj = {
        id: copyState.notificationsInformation.notificationsNextNumber,
        title: action.title,
        description: action.description,
        type: action.notificationType,
        isActive: true,
        initTime: Date.now(),
      }

      //ochichaem poslednii esli perepolnen
      if (newArray.length > 50) {
        newArray.pop()
      }

      //dobalyaem notification v nachalo
      newArray.unshift(resultNotificationObj)

      copyState.notificationsInformation.notificationsList = newArray
      copyState.notificationsInformation.notificationsNextNumber = copyState.notificationsInformation.notificationsNextNumber + 1

      const notif = new Notification(
        action.title,
        {
          tag: 'TAG',
          body: action.description
        }
      )

      return copyState
    }

    case 'app/setNoActiveNotification' : {
      let copyState = {...state}

      copyState.notificationsInformation.notificationsList = copyState.notificationsInformation.notificationsList.map(
        (notification: resultNotificationObjType) => {
          if (notification.id === action.id) {
            notification.isActive = false
          }
          return notification
        }
      )

      return copyState
    }

    case 'app/deleteNotification': {
      let copyState = {...state}

      copyState.notificationsInformation.notificationsList = copyState.notificationsInformation.notificationsList.filter(
        notification => notification.id !== action.id)

      return copyState
    }

    case 'app/setCurrentDropTrade': {
      let copyState = {...state}

      copyState.tradesInformation.currentDropTrade = action.tradeItem

      return copyState
    }

    case 'app/dropHandlerTradeItem': {
      let copyState = {...state}

      let currentIndex: number
      let dropIndex: number

      copyState.tradesInformation.tradesList = [...copyState.tradesInformation.tradesList]

      if (copyState.tradesInformation.currentDropTrade !== null) {
        currentIndex = copyState.tradesInformation.tradesList.indexOf(copyState.tradesInformation.currentDropTrade)
        if (currentIndex !== -1) {
          copyState.tradesInformation.tradesList.splice(currentIndex, 1)
          dropIndex = copyState.tradesInformation.tradesList.indexOf(action.tradeItem)
          copyState.tradesInformation.tradesList.splice(dropIndex + 1, 0, copyState.tradesInformation.currentDropTrade)
        }
      }
      copyState.tradesInformation.currentDropTrade = null

      return copyState
    }

    default: {
      return state
    }
  }
}


const TradeListGenerator = (symbolCurrent: string,
                            priceLastUSDT: number,
                            averageVolumeRelative: number,
                            isSignal: signalIsSignalType,
                            oldArray: Array<resultTradesObjType>,
                            dataPrice: Array<number>,
                            dataVolume: Array<number>): Array<resultTradesObjType> => {

  const newArray: Array<resultTradesObjType> = oldArray.map((tradeItem: resultTradesObjType) => {
    if (tradeItem.symbol === symbolCurrent) {

      let percentLast = tradeItem.tradeDirection === 'BUY'
        ? ((priceLastUSDT - tradeItem.priceOpen) * 100 * tradeItem.initValueRate) / tradeItem.priceOpen
        : ((tradeItem.priceOpen - priceLastUSDT) * 100 * tradeItem.initValueRate) / tradeItem.priceOpen

      let percentMax: number
      if (percentLast > tradeItem.percentMax) {
        percentMax = percentLast
      } else {
        percentMax = tradeItem.percentMax
      }

      let percentSL: number
      if (percentMax > tradeItem.initValueLevelBU) {
        if (percentMax > tradeItem.initValueDeltaPrice) {
          percentSL = -1 * (percentMax - tradeItem.initValueDeltaPrice)
        } else {
          percentSL = -1 * (percentMax - tradeItem.initValueDeltaPrice)
        }
      } else {
        if (percentMax > 0) {
          let aaa = (tradeItem.initValueSL + tradeItem.initValueLevelBU - tradeItem.initValueDeltaPrice) / tradeItem.initValueLevelBU
          let bbb = percentMax * aaa
          percentSL = tradeItem.initValueSL - bbb
        } else {
          percentSL = tradeItem.initValueSL
        }
      }

      let priceSL = tradeItem.tradeDirection === 'BUY'
        ? tradeItem.priceOpen - (tradeItem.priceOpen * percentSL) / (100 * tradeItem.initValueRate)
        : tradeItem.priceOpen + (tradeItem.priceOpen * percentSL) / (100 * tradeItem.initValueRate)


      let upPrice = 0 as signalUpPriceType
      if (priceLastUSDT > tradeItem.priceLast) {
        upPrice = 1 as signalUpPriceType
      }
      if (priceLastUSDT < tradeItem.priceLast) {
        upPrice = -1 as signalUpPriceType
      }

      let alertTrade: alertTradeType = tradeItem.alertTrade
      if (isSignal !== 'NO') {
        if (tradeItem.tradeDirection === 'BUY') {
          if (isSignal === 'BUY') {
            alertTrade = 'DS'
          }
          if (isSignal === 'SELL') {
            alertTrade = 'RS'
          }
        } else {
          if (isSignal === 'BUY') {
            alertTrade = 'RS'
          }
          if (isSignal === 'SELL') {
            alertTrade = 'DS'
          }
        }
      } else {
        if (tradeItem.tradeDirection === 'BUY') {
          if (priceLastUSDT > tradeItem.priceTP) {
            alertTrade = 'TP'
          } else {
            if (priceLastUSDT < priceSL) {
              alertTrade = 'SL'
            } else {
              alertTrade = null
            }
          }
        } else {
          if (priceLastUSDT < tradeItem.priceTP) {
            alertTrade = 'TP'
          } else {
            if (priceLastUSDT > priceSL) {
              alertTrade = 'SL'
            } else {
              alertTrade = null
            }
          }
        }
      }

      priceSL = Math.round(priceSL * 10000) / 10000
      percentSL = Math.round(percentSL * 100) / 100
      percentLast = Math.round(percentLast * 100) / 100
      percentMax = Math.round(percentMax * 100) / 100

      let resultTradeObj: resultTradesObjType = {
        symbol: tradeItem.symbol, //symbol - perenositsya++
        priceOpen: tradeItem.priceOpen, //cena otkrutiya - perenositsya++

        priceLast: priceLastUSDT, //poslednyaya cena - obnovlyaetsya++
        percentLast: percentLast, //poslednyaya cena v procentah - obnovlyaetsya++

        averageVolumeRelative: Math.round(averageVolumeRelative * 100),

        priceTP: tradeItem.priceTP, //cena TP - perenositsya++
        percentTP: tradeItem.percentTP, //cena TP v procentah - perenositsya++

        priceSL: priceSL, //cena SL - obnovlyaetsya++
        percentSL: percentSL, //cena SL v procentah - obnovlyaetsya++

        percentMax: percentMax, //cena Max v procentah - obnovlyaetsya++

        initValueTP: tradeItem.initValueTP, //initValueTP - perenositsya++
        initValueSL: tradeItem.initValueSL, //initValueSL - perenositsya++
        initValueRate: tradeItem.initValueRate, //initValueRate - perenositsya++
        initValueLevelBU: tradeItem.initValueLevelBU, //initValueLevelBU - perenositsya++
        initValueDeltaPrice: tradeItem.initValueDeltaPrice, //initValueDeltaPrice - perenositsya++

        tradeDirection: tradeItem.tradeDirection, //trend pri otkrutii trenda - perenositsya++
        upPrice: upPrice, //napravlenie cenu s poslednego obnovleniya - obnovlyaetsya++
        alertTrade: alertTrade, //alert treida

        dataPrice: dataPrice,
        dataVolume: dataVolume,
      }
      return resultTradeObj
    } else {
      return tradeItem
    }

  })

  return newArray
}


