//thunk
export const searchSignalsTC = (symbolCurrent: string, symbolNumber: number) => {
  return async (dispatch: any) => {

    const configUSDT = {
      symbol: symbolCurrent,
      interval: '1m',
      limit: 1000,
    }

    let symbolCurrentBTC = symbolCurrent.slice(0, -4) + 'BTC'
    symbolCurrentBTC = symbolCurrentBTC === 'BTCBTC' ? 'BTCUSDT' : symbolCurrentBTC

    const configBTC = {
      symbol: symbolCurrentBTC,
      interval: '1m',
      limit: 5,
    }

    try {
      const dataUSDT = await binanceAPI.getKlines(configUSDT)
      const dataBTC = await binanceAPI.getKlines(configBTC)
      dispatch(searchSignalsAC(symbolCurrent, symbolNumber, dataUSDT, dataBTC))
    } catch (e) {
      dispatch(setNewNotificationAC(
        `Error`,
        `searchSignalsAC ${symbolCurrent} ${symbolNumber} error`,
        'ERROR'))
    }
  }
}