import React from "react";
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import s from './MainTrendIndicator.module.css'
import {mainTrendType, variableBitcoinMainTrendVariablesType} from "../../store/appReducer";
import cs from "../../common/styles/styles.module.css";

type propsType = {
  name: string
  variable: variableBitcoinMainTrendVariablesType
}

const MainTrendIndicator: React.FC<propsType> = props => {

  const {
    name,
    variable
  } = props

  const value = useSelector<AppRootStateType, mainTrendType>(
    store => store.app.bitcoinMainInformation[`${variable}`])

  return (
    <span className={cs.commonFlexBody}>
      <span>{name}:</span>
      <span className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}>{value}</span>
    </span>

  )
}

export default MainTrendIndicator